// scss-lint:disable ColorKeyword, ColorVariable, SpaceAfterComma

// Colors
//
// Use the `color()` function to add colors to CSS properties. Documentation is available in
// [John Albins Github site](http://johnalbin.github.io/chroma/).
//
// Markup: chroma-kss-markup.twig
//
// Style guide: 1.1

@import 'chroma';
@import 'chroma/kss';

// These colours make up the building blocks for component colors.
$chroma: define-default-color-scheme('Descriptive color names for use in "functional" color names below.');
$chroma: add-colors((
    'white':                  #fff,
    'grey-xlight':            #f9f9f9,
    'grey-light':             #e6e6e6,
    'grey':                   #7f7f7f,
    'grey-mid':               #6f6f6f, // lightest for text
    'grey-dark':              #4a4e57,
    'black':                  #0d121f,
    'blue-light':             #e8f1fa,
    'blue':                   #1976d2,
    'dark-blue':              #002664,
    'teal':                   #00a1b6,
    'blue-green':             #009688,
    'green-vibrant':          #3a863d,
    'green-light':            #6b9d41,
    'purple':                 #903f97,
    'orange':                 #f78f1e,
    'red':                    #e41e63,
    'translucent-shadow':     ('black' rgba .5),
    'translucent-overlay':    ('black' rgba .6),
    'translucent-black':      ('black' rgba .8),
));

// The primary color scheme provides components with colors.
// Primary colors should be defined in each component.
// @see src/base/text/_text.scss for an example.
$chroma: define-color-scheme('primary', 'Colors used by design components.');

$chroma-active-scheme: 'primary';
